const reducer = (state = INITIAL_STATE, action) => {
    switch (action.type) {
        case 'DETAIL':
            return {
                ...state, productId: action.payload
            }
        case 'LIST':
            return {
                ...state, productId: undefined
            }
        default:
            return state;
    }
};

export default reducer;